import React, {Component} from 'react';
import {Link} from "react-router-dom";

class SignUp extends Component {
    render() {
        return (
            <div className="container text-center">
                <div className="mt-5 mb-3">
                    <h1 className="h3 mb-3 font-weight-normal">Sign Up</h1>
                </div>
                <form className="form-sign-in">
                    <div className="form-label-group my-3">
                        <label htmlFor="inputName">Name</label>
                        <input type="text" id="inputName" className="form-control" placeholder="Name or Username"
                               required autoFocus/>
                    </div>
                    <div className="form-label-group my-3">
                        <label htmlFor="inputMobile">Mobile Number</label>
                        <input type="tel" id="inputMobile" className="form-control" placeholder="Mobile Number"
                               required autoFocus/>
                    </div>

                    <div className="form-label-group my-3">
                        <label htmlFor="inputEmail">Email address</label>
                        <input type="email" id="inputEmail" className="form-control" placeholder="Email address"
                               required autoFocus/>
                    </div>

                    <div className="form-label-group mb-3">
                        <label htmlFor="inputPassword">Password</label>
                        <input type="password" id="inputPassword" className="form-control" placeholder="Password"
                               required/>
                    </div>
                    <button className="btn btn-lg btn-primary btn-block my-3" type="submit">Sign Up</button>
                </form>
                <Link to="/sign-in" className="nav-link">Sign In</Link>
            </div>
        )
    }
}

export default SignUp;