export function getposts() {
  return posts;
}

export function getpost(id) {
  return posts.find(p => p._id === id);
}

export function deletepost(id) {
  let postInDb = posts.find(p => p._id === id);
  posts.splice(posts.indexOf(postInDb), 1);
  return postInDb;
}